<style scoped lang="less">
  .tip-class {
    margin-top: 15px; font-weight: bold; color: darkred;font-size: 18px;
    margin-bottom: 15px
  }
</style>

<template>
  <div class="component-wrapper-class" style="overflow: scroll">

    <span ref="wxtip" class="tip-class">长按二维码，加微信免费发布信息</span>
    <img :src="contactQRCode" style="width: 100%;background-color: green;"/>

  </div>
</template>

<script>
  export default {
    name: 'contact',
    data(){
      return {
        contactQRCode: 'https://upload-images.jianshu.io/upload_images/2801164-ee43dea3716f0667.jpeg?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240'
      }
    },
    mounted() {
      let _this = this
      this.count = 0
      this.timer = setInterval(function () {
        if (_this.count % 2 == 0){
          _this.$refs.wxtip.style.transform='translateY(8px)';
        } else {
          _this.$refs.wxtip.style.transform='translateY(-8px)';
        }
        _this.count ++
      }, 1000)
    },
    methods: {
    }
  }
</script>

